.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 170px;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 60px;
  font-size: 15px;
}

.nav-connect {
  padding: 20px 20px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 16px;
  background: linear-gradient(267deg, #ff9a9e 0.36%, #3a1c71 102.06%);
  transition: 0.5s;
}

.nav-connect:hover {
  transform: scale(1.05);
}

.nav-menu li {
  display: flex;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
}

.nav-menu li img {
  display: flex;
  margin: auto;
}

.anchor-link {
  text-decoration: none;
  color: white;
}

.nav-mob-open {
  display: none;
}
.nav-mob-close {
  display: none;
}

.nav-connect a {
  color: white;
  text-decoration: none;
}

@media (max-width: 768px) {
  .navbar {
    margin: 20px 50px;
  }

  .nav-connect {
    display: none;
  }

  .nav-mob-open {
    display: block;
    position: fixed;
    right: 30px;
    width: 25px;
  }

  .nav-mob-close {
    display: block;
    position: relative;
    top: 30px;
    left: 200px;
    width: 20px;
  }

  .nav-menu {
    position: fixed;
    flex-direction: column;
    align-items: start;
    top: 0;
    gap: 30px;
    background-color: #542d77;
    width: 260px;
    height: 100%;
    z-index: 2;
    transition: right 0.5s;
    right: -350px;
  }

  .nav-menu li {
    font-size: 18px;
    padding-left: 60px;
    flex-direction: row;
    gap: 20;
  }
}
